.form {
    width: 100%;
    height: auto;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    padding: 24px 30px 30px;
    @include max(1024) {
        padding: 24px 30px 30px;
    }
    @include max(768) {
        padding: 0;
        padding-bottom: 30px;
    }
    &__title {
        font-size: 24px;
        font-weight: 600;
        line-height: 1.2;
        color: #2B3940;
        @include max(1024) {
            font-size: 24px;
        }
        @include max(768) {
            font-size: 22px;
            padding: 17px 57px 17px 20px;
            box-shadow: 0px 15px 20px 0px rgba(34, 29, 55, 0.04);
        }
    }
    &__text {
        font-size: 16px;
        font-weight: 500;
        line-height: 1.3;
        color: black;
        margin-top: 20px;
        @include max(1024) {
            font-size: 16px;
            margin-top: 20px;
        }
        @include max(768) {
            font-size: 16px;
            margin-top: 20px;
            padding: 0 20px;
        }
    }
    &__fields {
        display: flex;
        flex-direction: column;
        gap: 15px;
        margin-top: 24px;
        margin-bottom: 15px;
        @include max(1024) {
            gap: 15px;
            margin-top: 24px;
            margin-bottom: 15px;
        }
        @include max(768) {
            padding: 0 20px;
            gap: 15px;
            margin-top: 20px;
            margin-bottom: 15px;
        }
    }
    .field {
        &__input {
            padding: 11px 15px;
            font-size: 13px;
            line-height: 1.2;
            min-height: 40px;
            @include max(1024) {
                padding: 11px 15px;
                font-size: 13px;
                min-height: 40px;
            }
            @include max(768) {
                padding: 11px 15px;
                font-size: 13px;
                min-height: 40px;
            }
        }
        &__textarea {
            padding: 16px 15px;
            font-size: 13px;
            line-height: 1.2;
            min-height: 80px;
            @include max(1024) {
                padding: 16px 15px;
                font-size: 13px;
                min-height: 80px;
            }
            @include max(768) {
                padding: 16px 15px;
                font-size: 13px;
                min-height: 80px;
            }
        }
    }
    &__submit {
        margin-top: auto;
        width: 100%;
        border-radius: 11px;
        @include max(768) {
            border-radius: 11px;
        }
        @include max(768) {
            margin-left: auto;
            margin-right: auto;
            max-width: 335px;
            min-width: auto;
            border-radius: 11px;
        }
    }
    &__privacy {
        display: block;
        margin-top: 10px;
        font-size: 12px;
        font-weight: 500;
        line-height: 1.2;
        color: #B0B2B7;
        a {
            transition: color 0.3s linear;
            @include hover() {
                color: #1964AB;
            }
        }
        @include max(1024) {
            margin-top: 10px;
            font-size: 12px;
        }
        @include max(768) {
            margin-left: 20px;
            margin-right: 20px;
            margin-top: 10px;
            font-size: 12px;
        }
    }
}